body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --main-toolbars-bgcolor:#397175;
  --main-secondary-color: #E8A510;
  --main-bg-color: #3D3641;
  --main-title-text-color: #f2f2f2;
  --main-text-color: #333
}

.App{
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

h4 {
  margin-bottom:0px
}

.metal {
  outline: none; 
  color: hsla(0,0%,20%,1);
  background-color: hsl(0,0%,90%);
  transition: color .2s;
}

.radial.metal {
  background-image: -webkit-radial-gradient(  50%   0%,  8% 50%, hsla(0,0%,100%,.5) 0%, hsla(0,0%,100%,0) 100%),
    -webkit-radial-gradient(  50% 100%, 12% 50%, hsla(0,0%,100%,.6) 0%, hsla(0,0%,100%,0) 100%),
    -webkit-radial-gradient(   0%  50%, 50%  7%, hsla(0,0%,100%,.5) 0%, hsla(0,0%,100%,0) 100%),
    -webkit-radial-gradient( 100%  50%, 50%  5%, hsla(0,0%,100%,.5) 0%, hsla(0,0%,100%,0) 100%),
    
    -webkit-repeating-radial-gradient( 50% 50%, 100% 100%, hsla(0,0%,  0%,0) 0%, hsla(0,0%,  0%,0)   3%, hsla(0,0%,  0%,.1) 3.5%),
    -webkit-repeating-radial-gradient( 50% 50%, 100% 100%, hsla(0,0%,100%,0) 0%, hsla(0,0%,100%,0)   6%, hsla(0,0%,100%,.1) 7.5%),
    -webkit-repeating-radial-gradient( 50% 50%, 100% 100%, hsla(0,0%,100%,0) 0%, hsla(0,0%,100%,0) 1.2%, hsla(0,0%,100%,.2) 2.2%),
    
    -webkit-radial-gradient( 50% 50%, 200% 50%, hsla(0,0%,90%,1) 5%, hsla(0,0%,85%,1) 30%, hsla(0,0%,60%,1) 100%);
}

.metal.radial:before, .metal.radial:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: inherit;
  background-image: -webkit-radial-gradient(  50%   0%, 10% 50%, hsla(0,0%,0%,.1) 0%, hsla(0,0%,0%,0) 100%),
    -webkit-radial-gradient(  50% 100%, 10% 50%, hsla(0,0%,0%,.1) 0%, hsla(0,0%,0%,0) 100%),
    -webkit-radial-gradient(   0%  50%, 50% 10%, hsla(0,0%,0%,.1) 0%, hsla(0,0%,0%,0) 100%),
    -webkit-radial-gradient( 100%  50%, 50% 06%, hsla(0,0%,0%,.1) 0%, hsla(0,0%,0%,0) 100%);
}
.metal.radial:before { -webkit-transform: rotate( 65deg); transform: rotate( 65deg); }
.metal.radial:after { -webkit-transform: rotate(-65deg); transform: rotate(-65deg); }
.drum__box{
    min-width: 600px;
    max-width: 600px;
    max-height: 400px;
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #414141 74%);
    border: 1px solid rgb(114, 113, 113);
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    box-shadow: 1px 1px 2px 1px #333
}
.drum__controls{
    padding:10px;
    border-bottom:5px solid rgb(114, 113, 113);
    background-color: transparent;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center
}

.drum__controls-power {
    cursor:pointer;
    padding:10px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.drum__controls-power.on{
    color:green;
}

.drum__controls-power.off{
    color:red;
}

.drum__controls-display{
    padding:5px;
    background-color: #000;
    color: blue;
    min-width: 100px;
    min-height: 20px;
    border:2px solid rgb(114, 113, 113)
}

.drum__controls-display p{
    margin:0px
}

.drum__controls-volumen {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-between;
            justify-content: space-between
}

.drum__controls-volumen_display {
    padding:5px;
    background-color: #000;
    color: blue;
    min-width: 40px;
    min-height: 20px;
    border:2px solid rgb(114, 113, 113)
}

.drum__controls-volumen_display p{
    margin:0px
}

.drum__pads{
    padding:40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 10px 30px;
}

.drum-pad{
    min-height: 80px;
    background-color: #000;
    border:1px solid rgb(114, 113, 113);
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    color: var(--main-secondary-color);
}

.drum-pad:active{
    border:1px solid var(--main-secondary-color);
    color: black;
    background: var(--main-secondary-color);
}

.drum__controls-bank {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    width: 150px;
    color: var(--main-secondary-color)
}

.drum__controls-bank_selector {
    width: 50px;
    margin: 0px 5px;
    height: 20px;
    background: black;
    padding: 2px 2px;
    cursor: pointer
}

.drum__controls-bank_button {
    width:20px;
    height: 20px;
    background: var(--main-secondary-color);
}
 
 
  
